// ==============================
// COLORS, BACKGROUNDS, FONT COLORS
// ==============================

// Primary colors
$primary: #ff6600;
$secondary: #333333;
$accent: #f5a623;

// Status colors
$success: #28a745;
$error: #dc3545;
$info: #17a2b8;
$warning: #ffc107;

// Light and Gray Colors
$white: #ffffff;
$gray-50: #f9f9f9;
$gray-100: #f0f0f0;
$gray-200: #e0e0e0;
$gray-300: #d9d9d9;
$gray-400: #c0c0c0;
$gray-500: #a0a0a0;
$gray-600: #888888;
$gray-700: #666666;
$gray-800: #444444;
$gray-900: #222222;
$black: #000000;

// Pastel / light shades
$light-blue: #e0f7ff;
$light-green: #e6ffe0;
$light-yellow: #fff9e6;
$light-pink: #ffe6f0;
$light-orange: #fff3e0;

// Background variables
$bg-primary: $primary;
$bg-secondary: $secondary;
$bg-accent: $accent;
$bg-success: $success;
$bg-error: $error;
$bg-info: $info;
$bg-light: $white;
$bg-gray-50: $gray-50;
$bg-gray-100: $gray-100;

// Font color variables
$text-primary: $secondary;
$text-secondary: $gray-700;
$text-light: $white;
$text-accent: $accent;
$text-success: $success;
$text-error: $error;
$text-info: $info;
$text-gray-50: $gray-50;
$text-gray-100: $gray-100;

// Border colors
$border-primary: $primary;
$border-secondary: $secondary;
$border-light: $gray-200;
$border-dark: $gray-800;
